import { ConsoleLogger } from '@multimodal/mcp-agent';
import { BaseBrowserTool, MCPBrowserTool, CustomBrowserTool } from './base';
import { NavigationTools } from './navigation-tools';
import { ContentTools } from './content-tools';
import { InteractionTools } from './interaction-tools';

/**
 * BrowserToolsFactory - Factory for creating browser tool instances
 * 
 * Centralizes the creation of browser tool instances, ensuring consistent
 * configuration and initialization.
 */
export class BrowserToolsFactory {
  /**
   * Create a set of browser tools based on the specified mode
   * @param logger - Logger instance
   * @param mode - Browser control mode
   * @returns Array of browser tool providers
   */
  static createTools(
    logger: ConsoleLogger,
    mode: 'mixed' | 'browser-use-only' | 'gui-agent-only' = 'mixed'
  ): BaseBrowserTool[] {
    // Create tools based on mode
    const tools: BaseBrowserTool[] = [];
    
    // Add navigation tools
    tools.push(new NavigationTools(logger));
    
    // Add content tools with custom implementation
    tools.push(new ContentTools(logger));
    
    // Add interaction tools
    tools.push(new InteractionTools(logger));
    
    return tools;
  }
}
